



















































































































.wtc_setCardPosition-table {
    border-left: 1px solid #d9d9d9;
    font-size: 12px;
    color: #212121;
    background-color: #fff;
    width: 801px;
}

.wtc_setCardPosition-table .wtc_setCardPosition-table-header {
    display: flex;
    height: 40px;
    line-height: 40px;
}
.wtc_setCardPosition-table-header .wtc_setCardPosition-table-header-item{
    /*position: relative;*/
    display: flex;
    justify-content: center;
    flex: 0 0 200px;
    /*text-align: center;*/
    border-bottom: 1px solid #d9d9d9;
    border-right: 1px solid #d9d9d9;
    border-top: 1px solid #d9d9d9;
}
.wtc_setCardPosition-table-header-item .wtc_setCardPosition-table-body-tips-icon {
    display: block;
    margin-left: 6px;
    cursor: pointer;
    color: #666;
    height: 16px;
    line-height: 40px;
}
/*.wtc_setCardPosition-table-body-tips-icon {*/
    /*position: absolute;*/
    /*top: 12px;*/
    /*left: 67%;*/
/*}*/

.wtc_setCardPosition-table-body .wtc_setCardPosition-table-body-row{
    display: flex;
    height: 40px;
}
.wtc_setCardPosition-table-body .wtc_setCardPosition-table-body-row .wtc_setCardPosition-table-body-col{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 200px;
    text-align: center;
    border-bottom: 1px solid #d9d9d9;
    border-right: 1px solid #d9d9d9;
}
.wtc_setCardPosition-table-body-row .wtc_setCardPosition-table-body-col-text {
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
}
.wtc_setCardPosition-table-body-row .wtc_setCardPosition-table-body-col .wtc_setCardPosition-table-body-btn {
    position: relative;
    width: 40px;
    height: 24px;
    border-radius: 12px;
    padding: 2px;
    box-sizing: border-box;
}
.wtc_setCardPosition-table-body-btn .wtc_setCardPosition-table-body-btn-inner {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
}

.wtc_setCardPosition-btn-hidden {
    display: none;
}
.wtc_setCardPosition-btn-close{
    background-color: #bbb;
    cursor: pointer;
}
.wtc_setCardPosition-btn-open {
    background-color: var(--themeColor, #276FF5);
    cursor: pointer;
}
.wtc_setCardPosition-btn-close .wtc_setCardPosition-table-body-btn-inner{
    float: left;
}
.wtc_setCardPosition-btn-open .wtc_setCardPosition-table-body-btn-inner{
    float: right;
}
.wtc_setCardPosition-btn-disable {
    cursor: not-allowed;
    opacity: 0.5;
}

.wtc_setCardPosition-table-body-empty {
    border-right: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    height: 80px;
    line-height: 80px;
    text-align: center;
    color: #212121;
}
